@import url('https://fonts.googleapis.com/css?family=Oswald|Roboto|Roboto+Condensed&display=swap');

$primary-color: #781820;
$font_family_1: 'Oswald', sans-serif;
$font_family_2: 'Roboto', sans-serif;

h1 {
  color: $primary-color;
  font-family: $font_family_1;
  font-size: 4vw;
  font-weight: bold;
  margin: 2px;
  padding: 2px;
}

a:active {
  background-color: white;
  color: $primary-color;
}

.card {
  display: inline-block;
  padding: 2px;
  justify-content: center;
}

.card-title {
  font-family: $font_family_1;
  display: block;
  font-size: 2vw;
  font-weight: bolder;
  margin: 4px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  justify-content: center;
}

// .label {
//   display: none;
// }

.btn-primary {
  background-color: $primary-color;
  color: white;
  font-family: $font_family_1;
  text-align: center;
  font-size: 1.6vw;
  margin: 0px;
  padding: .375rem .75rem;
  vertical-align: middle;
  border: 0px;
  border-radius: .25rem;
}

.btn-primary:hover {
  background-color: white;
  color: $primary-color;
  border: white;
}

.btn-primary:active {
  background-color: $primary-color;
  color: white;
  border: white;
}

@media screen and (min-width: 1200px) {

  .card-title {
    font-size: 24px;
  }
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 18px;
  }

  .card-title {
    font-size: 16px;
  }
}